import React from "react";
import { Typography } from "@mui/material";
import Link from "@mui/material/Link";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import map_available from "../../images/map_available.svg";

const style = {
  width: "100%",
  maxWidth: "350px",
  margin: "0 auto",
};

type Props = {
  building_name: string;
  imagelink_name: string;
  id: number;
  map?: boolean;
};
const BuildingLink: React.FC<Props> = ({
  building_name,
  imagelink_name,
  id,
  map = false,
}) => {
  let link: string = "/content_en/" + encodeURIComponent(id) + "/1";
  let map_elem: JSX.Element | null = null;
  if (map) {
    map_elem = (
      <img src={map_available} style={{ width: "20px", marginLeft: "auto" }} />
    );
  }
  return (
    <Link underline="none" href={link}>
      <div
        style={{
          width: 350,
          marginLeft: "auto",
          marginRight: "auto",
          marginTop: -5,
        }}
      >
        <ListItem button sx={{ display: "flex" }}>
          <div style={{ marginLeft: -14 }}>
            <img
              width="65"
              src={`${process.env.PUBLIC_URL + imagelink_name}`}
            />
          </div>
          <Typography
            gutterBottom
            variant="h5"
            component="h3"
            style={{
              fontSize: 17,
              marginTop: 0,
              marginLeft: 15,
              color: "#3C4043",
            }}
          >
            {building_name}
          </Typography>
          {map_elem}
        </ListItem>
        <Divider
          style={{
            width: "auto",
            marginLeft: 0,
            marginRight: 0,
            color: "#696969",
            marginTop: -8,
          }}
        ></Divider>
      </div>
    </Link>
  );
};
