import Header from "./component/header";
import { HowToUse } from "./component/HowToUse";
import { InsideBuilding } from "./component/InsideBuilding";
import { Rooting } from "./component/Rooting";
import { BottomNavigation } from "./component";
import React, { useEffect } from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { useParams } from "react-router";
import icon_compass from "../images/icon_compass.svg";
import icon_map_and_building from "../images/icon_map_and_building.svg";
import icon_menu from "../images/icon_menu.svg";

const Main: React.FC = () => {
  const { id } = useParams<"id">();
  const [value, setValue] = React.useState(Number(id));
  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  useEffect(() => {
    (async () => {})();
  }, []);

  return (
    <div>
      <div style={{ zIndex: 19, position: "fixed", width: "100%" }}>
        {value == 0 ? (
          <Header page_name="メニュー" />
        ) : value == 1 ? (
          <Header page_name="経路案内" />
        ) : (
          <Header page_name="建物・地図一覧" />
        )}
      </div>

      <div style={{ height: 41 }} />
      <div style={{ zIndex: 9, width: "100%", marginTop: -40 }}>
        {value === 0 ? (
          <div style={{ position: "relative", zIndex: 18 }}>
            <HowToUse />
          </div>
        ) : value === 1 ? (
          <div style={{ position: "relative", zIndex: 18 }}>
            <Rooting />
          </div>
        ) : (
          <div style={{ position: "relative", zIndex: 18 }}>
            <InsideBuilding />
          </div>
        )}

        <BottomNavigation value={value} onChange={handleChange} />
      </div>
    </div>
  );
};
export default Main;
