import React from "react";
import { Link, Typography } from "@mui/material";

export const ChangeLanguage: React.FC<{
  lang?: "jp" | "en";
  href?: string;
  img_src?: string;
}> = ({
  lang = "jp",
  href = "",
  img_src = `${process.env.PUBLIC_URL}/images/globe.svg`,
}) => {
  let en_fontweight: { fontWeight?: "lighter" | "bold" } = {
    fontWeight: "lighter",
  };
  let jp_fontweight: { fontWeight?: "lighter" | "bold" } = {
    fontWeight: "bold",
  };
  if (lang === "en") {
    en_fontweight = { fontWeight: "bold" };
    jp_fontweight = { fontWeight: "lighter" };
  }
  return (
    <Link underline="none" href={href}>
      <div
        style={{
          display: "flex",
          alignItems: "center",
        }}
      >
        <img src={img_src} alt="Image" width="20px" />
        <Typography
          display="inline"
          sx={{ marginLeft: "5px", color: "#fff", ...jp_fontweight }}
        >
          JP
        </Typography>
        <Typography display="inline" sx={{ color: "#fff", margin: "3px" }}>
          |
        </Typography>
        <Typography display="inline" sx={{ color: "#fff", ...en_fontweight }}>
          EN
        </Typography>
      </div>
    </Link>
  );
};
