import React from "react";
import { Routes as RouteList, Route, Navigate } from "react-router-dom";
import {
  Main,
  Building,
  BuildingRoom,
  Usage,
  UsageMap,
  UsageRooting,
  Question,
  MainEn,
  BuildingEn,
  BuildingRoomEn,
  UsageEn,
  UsageMapEn,
  UsageRootingEn,
  QuestionEn,
} from "./views";

interface Props {
  path: string;
  exact?: boolean;
  auth?: boolean;
  component: any;
  layout: any;
}

const Routes: React.FC = () => {
  return (
    <RouteList>
      <Route path="/" element={<Navigate replace to="/main/1" />} />
      <Route path="/content/:id/:ids" element={<Building />} />
      <Route path="/room/:id/:ids" element={<BuildingRoom />} />
      <Route path="/usage" element={<Usage />} />
      <Route path="/usagemap" element={<UsageMap />} />
      <Route path="/usagerooting" element={<UsageRooting />} />
      <Route path="/question" element={<Question />} />
      <Route path="/main/:id" element={<Main />} />
      <Route path="/content_en/:id/:ids" element={<BuildingEn />} />
      <Route path="/room_en/:id/:ids" element={<BuildingRoomEn />} />
      <Route path="/usage_en" element={<UsageEn />} />
      <Route path="/usagemap_en" element={<UsageMapEn />} />
      <Route path="/usagerooting_en" element={<UsageRootingEn />} />
      <Route path="/question_en" element={<QuestionEn />} />
      <Route path="/main_en/:id" element={<MainEn />} />
      <Route element={<Navigate replace to="/not-found" />} />
    </RouteList>
  );
};

export default Routes;
